.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.middle-box {
  background-color: #282c34;
  display: flex;
}

.content-box {
  background-color: #d4dae4;
  display: flex;
  height: 42rem; 
}

.footer-box {
  background-color: #282c34;
  display: flex;
  justify-content: center; 
  align-items: center; 
  color: #FFFFFF;
  height: 5rem; 
  text-align: center;
}

.footer-box h2 {
  text-align: center;
  align-content: center;
  color: #FFFFFF;
}

.nav-box {
  background-color: #264585;
  display: flex;
}

.nav-box ul {
  list-style-type: none; 
}

.nav-box ul li a {
  color: #ffffff; 
  text-decoration: none; 
}

.nav-box ul li a:hover {
  color: #000000; 
}

.nav-box ul li a:active {
  color: #000000; 
}
.nav-box ul li a:focus {
  color: #1fa361;
}
.nav-box ul li a:visited:focus {
  color: #1fa361;
}
.nav-box ul li a:visited {
  color: #ffffff; 
}
.nav-box ul li a:visited:hover {
  color: #000000; 
}
.App-link {
  color: #61dafb;
}

.App-link:hover {
  color: #000000;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
